import axios from 'axios';

export default {
    namespaced: true,
    state: () => ({
        userMessages: []
    }),
    getters: {
        userMessages: (state) => state.userMessages
    },
    mutations: {
    },
    actions: {
       getPrivateMessages: async function(context){
            try{
                const url =  opentext.mainurl + getApis().getPrivateMessages + `&userId=${opentext.userId}`;
                const response = await axios.get(url, { headers: { 
                    'OTCSTicket': opentext.OTCSTicket, 
                }});
                const data = JSON.parse(response.data.data).result;
                data.pop();
                context.state.userMessages = data;
            }catch(err){
                console.log(err);
            }
       },
       sendMessage: async function(context, payload){
            const formdata = new FormData();
            formdata.append("ATT_ID", "0");
            formdata.append("ATT_PathSaved", "");
            formdata.append("text", payload.msg);
            formdata.append("ATT_Path", "");
            formdata.append("post_audience", "2");
            formdata.append("user", payload.user);

            const config = {
              method: 'post',
              url: `${opentext.mainurl}/pulse/direct_messages/new`,
              data : formdata
            };
            try{
                const response = await axios(config)
                return response.data.direct_message;
            }catch(error){
                console.log(error);
            };
       },
       sendMessagesToFirebase: function(context) {
       }
    }
}